.container {
    flex: 1; 
    display: flex; 
    flex-direction: column;
}

.indicators {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.indicator {
    height: 50px;
    width: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.indicator p {
    font-size: 40px;
}

.scroller {
    flex: 4;
    display: flex;
}

.scroller .body {
    flex: 2;
    display: flex;
    justify-content: center;
    align-items: center;
}

.scroller .body::-webkit-scrollbar {
    display: none;
}

.scroller .button-container {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.scroller .button {
    height: 4vh;
    width: 4vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media only screen and (max-width: 1000px) {
    .container {
        max-height: 8vh;
    }
    .scroller .button-container {
        flex: 2;
    }
}