.profile {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.profile img {
    height: 20vw;
    width: 20vw;
    border-radius: 10vw;
}

.profile h4{
    color: gray;
    font-size: larger;
}

.profile h1 {
    font-size: xxx-large;
}

.profile .detail {
    height: 10vh;
    width: 10vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.profile .image-container {
    flex: 3;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.profile .info-container {
    flex: 2;
    display: flex;
    flex-direction: column;
}

.profile .info-container .margin-top {
    flex: 1;
}

.profile .info-container .margin-bottom {
    flex: 1;
}

.profile .info-container .margin-left {
    flex: 0.5;
}

.profile .info {
    flex: 1; 
    display: flex;
    justify-content: center;
}

.portrait {
    visibility: hidden;
}

.portrait .tab {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.portrait .tab .button {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40vw;
    height: 6vh;
    border-radius: 40px;
}

.landscape {
    flex: 1;
    display: flex;
    flex-direction: column;
}

.landscape .heading {
    flex: 1;
    display: flex;
    align-items: center;
}

.landscape .content {
    flex: 4;
    display: flex;
}

/* For tablets and smaller ones */
@media only screen and (max-width: 1000px) {
    body {
        flex-direction: column;
    }
    .profile {
        flex-direction: row;
    }

    .profile img {
        height: 30vw;
        width: 30vw;
        border-radius: 20vw;
    }

    .profile .image-container {
        flex: 2;
        justify-content: flex-end;
        align-items: center;
    }
    
    .profile .info-container {
        flex: 3;
    }
    
    .profile .info-container .margin-left {
        flex: 0.1;
    }

    .profile .info {
        justify-content: flex-start;
    }

    .profile .detail {
        width: 20vw;
    }

    .landscape {
        flex: 0;
        visibility: hidden;
    }

    .portrait {
        visibility: visible;
        flex: 3;
        display: flex;
        flex-direction: column;
    }
}
