.headerbar {
    flex: 2;
    display: flex;
    min-height: 10vh;
    position: fixed;
    top: 0;
    width: 100%;
}

.header {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.sticky {
    position: fixed;
    top: 100;
    width: 82%;
}

.reader {
    flex: 12;
    width: 100%;
    display: flex;
    flex-direction: column;
}

.reader .top {
    flex: 2;
    display: flex;
    min-height: 10vh;
    margin-bottom: 1vh;
}

.reader .heading {
    flex: 9;
    display: flex;
    flex-direction: column;
    margin-bottom: 3vh;
}

.reader .heading h1 {
    font-size: 500%;
}

.reader .heading h3 {
    color: gray;
    font-size: 200%;
    margin-bottom: 2vh;
}

.reader .margin {
    flex: 2;
}

.reader .content {
    flex: 10;
    overflow: scroll;
    white-space: pre-line;
}

.reader .content::-webkit-scrollbar {
    display: none;
}

.reader .content p {
    font-size: 150%;
}

.reader .content h5 {
    font-size: 180%;
    margin: 10px 0px 10px 0px;
}

.reader .content #image {
    margin: 20px 0 5px 0;
    width: 40vw;
}

@media only screen and (max-width: 1000px) {
    .header {
        flex: 2;
    }
    .reader .heading h1 {
        font-size: 300%;
    }
    .reader .heading h3 {
        font-size: 150%;
    }
    .reader .margin {
        flex: 1;
    }
    .reader .content p {
        font-size: 100%;
    }
    .reader .content #image {
        width: 85vw;
    }
}