.card {
    display: flex;
    min-height: 25vh;
    border-radius: 10px;
    width: 20vw;
    background-color: #333333;
    box-shadow: 0 4px 10px 0 rgba(242, 242, 242, 0.2), 0 6px 20px 0 rgba(242, 242, 242, 0.19);
}

.card img {
    height: 4vh;
    width: 4vh;
    border-radius: 10%;
}

.card.show .image {
  flex: 2;
  display: flex;
  justify-content: center; 
  align-items: center;
}

.card.hide .image {
  flex: 2;
  display: flex;
  justify-content: center; 
  align-items: center;
}

.card.show .heading {
  flex: 3;
  display: flex;
  justify-content: flex-end;
}

.card.hide .heading {
  flex: 3;
  display: flex;
  justify-content: flex-end;
}

.card.hide {
    -webkit-animation: fadeinout .3s ease 0s 1;
    animation: fadeinout .3s ease 0s 1;
  }
  
@-webkit-keyframes fadeinout {
  0% { opacity: 0.5; }
  100% { opacity: 0; }
}
  
@keyframes fadeinout {
  0% { opacity: 0.5; }
  100% { opacity: 0; }
}
  
.card.show {
  -webkit-animation: display .5s ease 0s 1;
  animation: display .5s ease 0s 1;
}
  
@-webkit-keyframes display {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
  
@keyframes display {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@media only screen and (max-width: 1000px) {
    .card {
        width: 70vw;
    }
    .card.show .heading {
      flex: 2;
    }
    .card.hide .heading {
      flex: 2;
    }
}

@media only screen and (max-width: 500px) {
  .card.show .image {
    flex: 3;
  }
  .card.hide .image {
    flex: 3;
  }
}