body {
  margin: 0;
  min-height: 95vh;
  width: 100vw;
  display: flex;
  font-family: 'Noto';
  overflow-x: hidden;
  touch-action: pan-y;
  background-color: black;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body::-webkit-scrollbar {
  display: none;
}

body p {
  margin-block: 0em;
}

body h1 {
  margin-block: 0em;
}

body h2 {
  margin-block: 0em;
}

body h3 {
  margin-block: 0em;
}
body h4 {
  margin-block: 0em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Noto';
  src: local('Noto'), url(../assets/fonts/Noto.ttf) format('truetype');
}