.App {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.App body {
  flex: 14;
  display: flex;
}

.App footer {
  flex: 0.5;
  display: flex;
  flex-direction: column;
  color: gray;
}

.App footer .icon {
  height: 3vh;
  width: 3vh;
  margin: 0vh 0.5vh 0vh 0.5vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App footer .top {
  flex: 1;
  display: flex;
  justify-content: center;
}

.App footer .bottom {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}